import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatTableDataSource, MatPaginator, MatSnackBar } from '@angular/material';
import { PageEvent } from '@angular/material/paginator';
import { MotivosContingencia } from 'src/app/models/motivos-contingencia.model';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MotivosContingenciaService } from '../services/motivos-contingencia.service';

@Component({
	selector: 'app-motivos-contingencia',
	templateUrl: './motivos-contingencia.component.html',
	styleUrls: ['./motivos-contingencia.component.scss']
})

export class MotivosContingenciaComponent implements OnInit {
	
	public formAddMotivosContingencia: FormGroup;
	public formSearch: FormGroup;
	public showErrors: boolean;
	public displayedColumns: string[] = ['nome', 'ativo', 'editar'];
	public dataSource = new MatTableDataSource<MotivosContingencia>([]);
	public length: number;
	public pageSize = 10;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [10, 25, 100];
	public period: string = '1';
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public pouchs: MotivosContingencia[] = [];
	public loader: boolean = false;

	constructor(
		private formBuilder: FormBuilder,
		private MotivosContingenciaService: MotivosContingenciaService,
		private matSnackBar: MatSnackBar
	) { }
	
	ngOnInit() {
		this.formSearch = this.formBuilder.group({
			descricaoMotivo : ['']
		});
		
		this.formAddMotivosContingencia = this.formBuilder.group({
			motivoContingencia : ['', Validators.required],
			ativo : ['', Validators.required]
		});
		this.getMotivosContingencia();
	}

	public getMotivosContingencia(): void {
		this.pouchs = [];
		const pageIndex = (this.pageIndex  + 1).toString();
		const pageSize = this.pageSize.toString();
		this.loader = true;
		this.MotivosContingenciaService.getAll(this.formSearch.value.descricaoMotivo, pageSize, pageIndex).subscribe(
			response => {
				this.pouchs = [...response.data];
				this.dataSource = new MatTableDataSource<MotivosContingencia>(this.pouchs);
				this.length = response.length;
				this.dataSource.paginator = this.paginator; 
				this.loader = false;
			},
			error => {
				this.loader = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getMotivosContingencia();
	}

}
