import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { ResponseList } from 'src/app/models/response-list.interface';
import { MotivosContingencia } from 'src/app/models/motivos-contingencia.model';

@Injectable({
	providedIn: 'root'
})
export class MotivosContingenciaService {

    public motivosContingenciaUrl="/api/v1/motivos-contingencia"

    constructor (
        private http: HttpClient,
    ) {}

    public getAll(descricaoMotivo: string, pageSize: string, pageIndex: string): Observable<any> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosContingenciaUrl, {
            params : {
                DescricaoMotivo: descricaoMotivo,
                PageSize: pageSize,
                PageIndex: pageIndex
            }
        })
    }

    public getAtividades(): Observable<any> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosContingenciaUrl + '/novo');
    }

    public get(motivoContingenciaId: string): Observable<any> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosContingenciaUrl + '/' + motivoContingenciaId)
    }

    public post(nome: string, ativo: boolean,  atividades: any): Observable<any> {
        return this.http.post<MotivosContingencia>(environment.urlServices + this.motivosContingenciaUrl, {
            "nome": nome,
            "ativo": ativo,
            "atividades": atividades
        })
    }

    public put(motivoContingenciaId: string, nome: string, ativo: boolean,  atividades: any): Observable<any> {
        return this.http.put<MotivosContingencia>(environment.urlServices + this.motivosContingenciaUrl + '/' + motivoContingenciaId, {
            "nome": nome,
            "ativo": ativo,
            "atividades": atividades
        })
    }

    public delete(id: number): Observable<ResponseList> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosContingenciaUrl + '/delete/' + id)
    }
}