import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { ManagerTemplateService } from '../services/manager-templates.service';
import { MatPaginator, MatTableDataSource, MatSnackBar } from '@angular/material';
import { tap } from 'rxjs/operators';
import { TemplateList } from 'src/app/models/template-list.interface';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';

@Component({
	selector: 'app-list-template',
	templateUrl: './list-template.component.html',
	styleUrls: ['./list-template.component.scss']
})
export class ListTemplateComponent implements OnInit, AfterViewInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public dataSource = new MatTableDataSource<TemplateList>();
	public displayedColumns: string[] = ['id', 'templateName', 'activity', 'actions'];
	public totalItens: number = 0;
	public loading: boolean;
	
	constructor(
		private managerTemplateService: ManagerTemplateService,
		private matSnackBar: MatSnackBar
	) { }
	
	ngOnInit() {
		this.loading = true;
		sessionStorage.removeItem('postTemplate');
	}

	ngAfterViewInit() {
		this.getListTemplates();
		if (this.paginator) {
			this.paginator.page.pipe(
				tap(() => this.getListTemplates())
			).subscribe();
		}
	}
	
	private getListTemplates(): void {
		this.loading = true;
		const pageIndex: string = this.paginator ? (this.paginator.pageIndex +1).toString() : '1';
		const pageSize: string = this.paginator ? this.paginator.pageSize.toString() : '5';

		this.managerTemplateService.getListTemplates(pageIndex, pageSize).subscribe(
			response => {
				this.totalItens = response.length;
				this.dataSource = new MatTableDataSource<TemplateList>(response.data);
				this.loading = false;
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Não foi possível carregar a lista de templates");
				this.loading = false;
			}
		);
	}

	public applyFilter(filterValue: string) {
		this.dataSource.filter = filterValue.trim().toLowerCase();
	}
	
}
