import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MotivosRejeicao } from 'src/app/models/motivos-rejeicao.model';
import { MatTableDataSource, MatPaginator, MatSnackBar, MatDialog } from '@angular/material';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MotivosRejeicaoService } from '../services/motivos-rejeicao.service';
import { ActivatedRoute, Router } from '@angular/router';
import { DialogMotivosRejeicaoComponent } from './dialog-motivos-rejeicao/dialog-motivos-rejeicao.component';
import { Tree } from 'src/app/models/tree.model';
import { Fila } from 'src/app/models/fila.model';
import { UsuarioFila } from 'src/app/models/usuario-fila.interface';
import { FilaMotivos, Operacoes, Atividades } from 'src/app/models/fila-motivos.model';

@Component({
	selector: 'app-motivos-rejeicao',
	templateUrl: './add-motivos-rejeicao.component.html',
	styleUrls: ['./add-motivos-rejeicao.component.scss']
})

export class AddMotivosRejeicaoComponent implements OnInit {
	
	public form: FormGroup;
	public showErrors: boolean;
	public loader: boolean = false;
	public motivoRejeicaoId: string;
	public motivosRejeicao = new MotivosRejeicao();
	public tipoStatus = [
		{ label: 'Ativo', status: true },
		{ label: 'Inativo', status: false },
	];
	public treeFila: Tree[];
	public fila = [];
	public filas: [] = [];
	public dataSource = new MatTableDataSource<UsuarioFila>();
	public displayedColumns: string[] = ['grupo', 'operacao', 'atividade'];
	public checkedList: Fila[] = [];

	constructor(
		private formBuilder: FormBuilder,
		private MotivosRejeicaoService: MotivosRejeicaoService,
		private matSnackBar: MatSnackBar,
		private activatedRoute: ActivatedRoute,
		public dialog: MatDialog,
		private router: Router,
	) { 
		
	}
	
	ngOnInit() {
		this.motivoRejeicaoId = this.activatedRoute.snapshot.paramMap.get('motivoRejeicaoId');
		this.form = this.formBuilder.group({
			nome : ['', Validators.required],
			ativo : ['', Validators.required]
		});
		if(this.motivoRejeicaoId != null){
			this.getMotivoRejeicaoId();
		}else{
			this.getMotivoRejeicao();
		}
	}

	private getMotivoRejeicaoId(): void {
		this.loader = true;
		this.MotivosRejeicaoService.get(this.motivoRejeicaoId).subscribe(
			response => {
				this.motivosRejeicao = response.data;
				this.form.controls['nome'].setValue(this.motivosRejeicao.nome);
				this.form.controls['ativo'].setValue(this.motivosRejeicao.ativo);
				this.loader = false;
				this.filas = this.motivosRejeicao.operacaoGrupos;
				this.dataTree(this.filas);
			},
			error => {
				this.loader = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	private getMotivoRejeicao(): void {
		this.loader = true;
		this.MotivosRejeicaoService.getAtividades().subscribe(
			response => {
				this.motivosRejeicao = response.data;
				this.loader = false;
				this.filas = this.motivosRejeicao.operacaoGrupos;
				this.dataTree(this.filas);
			},
			error => {
				this.loader = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	public dataTree(fila: any){
		let tempTree = [];
		//Grupo operacoes
		fila.map(grupo=> {
			let grupoTemp = new Tree();
			grupoTemp.id = grupo.operacaoGrupoId;
			grupoTemp.name = grupo.operacaoGrupoNome;
				//operacoes
				grupo.operacoes.map(opera=> {
					let operacoesTemp = new Tree();
					operacoesTemp.id = opera.operacaoId;
					operacoesTemp.name = opera.operacaoNome;
					grupoTemp.children.push(operacoesTemp);
						//atividades
						opera.atividades.map(ativ=> {
							let atividadeTemp = new Tree();
							atividadeTemp.id = ativ.atividadeId;
							atividadeTemp.name = ativ.nome;
							atividadeTemp.checked = ativ.checked;
							operacoesTemp.children.push(atividadeTemp);
						});
				});
			tempTree.push(grupoTemp);
		});
		
		this.treeFila = tempTree;
		this.setList(this.treeFila);
	}

	public salvarAlteracoes(): void {
		if(this.form.valid){
			if(this.motivoRejeicaoId != null){
				this.atualizaMotivosRejeicao();
			} else {
				this.addMotivosRejeicao();
			}
		}
	}


	private addMotivosRejeicao(): void {
		if (this.form.valid) {
			this.showErrors = false;
			const form = this.form.value;
			this.loader = true;
			let atividades = [];
			this.checkedList.forEach(item=>{
				atividades.push(item.atividadeId);
			});
			this.MotivosRejeicaoService.post(form.nome, form.ativo, atividades).subscribe(
				() => {
					SnackbarComponent.showSuccess(this.matSnackBar);
					this.router.navigate(['configuracao/motivos-rejeicao']);
				}, error => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loader = false;
				}
			);
		} else {
			this.showErrors = true;
		}
	}

	private atualizaMotivosRejeicao(): void {
		if (this.form.valid) {
			this.showErrors = false;
			const form = this.form.value;
			this.loader = true;
			let atividades = [];
			 this.checkedList.forEach(item=>{
				atividades.push(item.atividadeId);
			 });
			this.MotivosRejeicaoService.put(this.motivoRejeicaoId, form.nome, form.ativo, atividades).subscribe(
				() => {
					SnackbarComponent.showSuccess(this.matSnackBar);
					this.loader = false;
					this.router.navigate(['configuracao/motivos-rejeicao']);
				}, error => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loader = false;
				}
			);
		} else {
			this.showErrors = true;
		}
	}

	public listaAtividades(): void {
	}

	public openModal() {
		const dialogRef = this.dialog.open(DialogMotivosRejeicaoComponent, {
			height: '80%',
  			width: '800px',
			data: this.treeFila
		});
		dialogRef.afterClosed().subscribe(result => {
			if(result !== undefined ) {
				//atualiza fila
				this.treeFila.forEach(item=>{
					item.children.forEach(subItem=>{
						subItem.children.forEach(atividade=>{
							result.dataNodes.forEach(newItem=>{
								if(newItem.level == 2 && newItem.id == atividade.id){
									atividade.checked = newItem.checked;
								}
							})
						})
					})
				});
			}
			this.setList(this.treeFila);
		});
	}

	private setList(tree: any){
		
		let tempList: Fila[] = [];
		//Grupo operacoes
		tree.map(grupo=> {
			   
			   	let gpTemp: any = [];
				gpTemp.operacaoGrupoId = grupo.id;
				gpTemp.operacaoGrupo = grupo.name;
				//operacoes
			   	grupo.children.map(opera=> {
					let opTemp: any = [];
					opTemp.operacaoId = opera.id;
					opTemp.operacao = opera.name;
					//atividades
					opera.children.map(ativ=> {
						let atTemp: any = [];
						atTemp.atividadeId = ativ.id;
						atTemp.atividade = ativ.name;
						atTemp.checked = ativ.checked;
						let grupoTemp = new Fila();
						grupoTemp.operacaoGrupoId = gpTemp.operacaoGrupoId;
						grupoTemp.operacaoGrupo = gpTemp.operacaoGrupo;
						grupoTemp.operacaoId = opTemp.operacaoId; 
						grupoTemp.operacao = opTemp.operacao;
						grupoTemp.atividadeId = atTemp.atividadeId;
						grupoTemp.atividade = atTemp.atividade;
						grupoTemp.checked = atTemp.checked;
						tempList.push(grupoTemp);
					});
			   	});
		});
		this.checkedList = tempList.filter(item=>item.checked);
	   	this.dataSource = new MatTableDataSource<Fila>(this.checkedList);
   	}

}
