import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { ResponseList } from 'src/app/models/response-list.interface';
import { map } from 'rxjs/operators';
import { PostTemplate } from 'src/app/models/post-template.class';

@Injectable({
	providedIn: 'root'
})
export class ManagerTemplateService {

    constructor (
        private http: HttpClient,
    ) {}

    public getListTemplates(pageIndex: string, pageSize: string, ): Observable<ResponseList> {
        return this.http.get<ResponseList>(`${environment.urlServices}/api/v1/templates`, {
            params : {
                PageSize: pageSize,
                PageIndex: pageIndex
            }
        })
    }

    public getTemplate(templateId: string): Observable<any> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/templates/${templateId}`).pipe(
            map(response => response.data)
        )
    }

    public postNewTemplate(template: PostTemplate): Observable<any> {
        return this.http.post<PostTemplate>(`${environment.urlServices}/api/v1/templates`, template)
    }

    public updateTemplate(template: PostTemplate, templateId): Observable<any> {
        return this.http.put<PostTemplate>(`${environment.urlServices}/api/v1/templates/${templateId}`, template)
    }
}