import { HttpClient } from '@angular/common/http';
import { Observable, empty } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { ResponseList } from 'src/app/models/response-list.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { ResponseObject } from 'src/app/models/response-object.interface';

@Injectable({
	providedIn: 'root'
})
export class ActivityService {
    private urlController: string = '/api/v1/atividades';
    private urlCentroCustos: string = '/api/v1/centro-custos';
    constructor (
        private http: HttpClient,
    ) {}


    public getAll(): Observable<ResponseList> {
        return this.http.get<ResponseList>(`${environment.urlServices}${this.urlController}`);
    }

    public find(pageIndex: string, pageSize: string, filter: string, operacaoId: string ): Observable<ResponseList> {
        return this.http.get<ResponseList>(`${environment.urlServices}${this.urlController}/listar`, {
            params : {
                PageSize: pageSize,
                PageIndex: pageIndex,
                Nome: filter,
                OperacaoId: operacaoId || ""
            }
        })
    }

    public get(id: string): Observable<ResponseObject> {
        if (id){
            return this.http.get<ResponseObject>(`${environment.urlServices}${this.urlController}/${id}`);
        }else{
            const simpleObservable = new Observable<ResponseObject>((observer) => {
                observer.next()
                observer.complete()
            })
            return simpleObservable;
        }
    }


    public save(entity: Atividade): Observable<any> {
        if (entity.id){
            return this.put(entity);
        }else{
            return this.post(entity);
        }
    }

    public post(entity: Atividade): Observable<any> {
        return this.http.post<Atividade>(`${environment.urlServices}${this.urlController}`, entity)
    }

    public put(entity: Atividade): Observable<any> {
        return this.http.put<Atividade>(`${environment.urlServices}${this.urlController}/${entity.id}`, entity)
    }

    public centroCustos(): Observable<ResponseObject> {
        return this.http.get<ResponseObject>(`${environment.urlServices}${this.urlCentroCustos}`);
    }
}