import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatTableDataSource, MatPaginator, MatSnackBar } from '@angular/material';
import { PageEvent } from '@angular/material/paginator';
import { MotivosRejeicao } from 'src/app/models/motivos-rejeicao.model';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MotivosRejeicaoService } from '../services/motivos-rejeicao.service';

@Component({
	selector: 'app-motivos-rejeicao',
	templateUrl: './motivos-rejeicao.component.html',
	styleUrls: ['./motivos-rejeicao.component.scss']
})

export class MotivosRejeicaoComponent implements OnInit {
	
	public formAddMotivosRejeicao: FormGroup;
	public formSearch: FormGroup;
	public showErrors: boolean;
	public displayedColumns: string[] = ['nome', 'ativo', 'editar'];
	public dataSource = new MatTableDataSource<MotivosRejeicao>([]);
	public length: number;
	public pageSize = 5;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public period: string = '1';
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public pouchs: MotivosRejeicao[] = [];
	public loader: boolean = false;

	constructor(
		private formBuilder: FormBuilder,
		private MotivosRejeicaoService: MotivosRejeicaoService,
		private matSnackBar: MatSnackBar
	) { }
	
	ngOnInit() {
		this.formSearch = this.formBuilder.group({
			descricaoMotivo : ['']
		});
		
		this.formAddMotivosRejeicao = this.formBuilder.group({
			motivoRejeicao : ['', Validators.required],
			ativo : ['', Validators.required]
		});
		this.getMotivosRejeicao();
	}

	public getMotivosRejeicao(): void {
		this.pouchs = [];
		const pageIndex = (this.pageIndex  + 1).toString();
		const pageSize = this.pageSize.toString();
		this.loader = true;
		this.MotivosRejeicaoService.getAll(this.formSearch.value.descricaoMotivo, pageSize, pageIndex).subscribe(
			response => {
				this.pouchs = [...response.data];
				this.dataSource = new MatTableDataSource<MotivosRejeicao>(this.pouchs);
				this.length = response.length;
				this.dataSource.paginator = this.paginator; 
				this.loader = false;
			},
			error => {
				this.loader = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getMotivosRejeicao();
	}

}
