import { Component, OnInit, ElementRef, ViewChild, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Perfil } from 'src/app/models/perfil.interface';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar, MatTableDataSource } from '@angular/material';
import { OperationService } from '../../services/operation.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { OperationGroupService } from '../../services/operation-group.service';

@Component({
	selector: 'app-operation-form',
	templateUrl: './operation-form.component.html',
	styleUrls: ['./operation-form.component.scss']
})
export class OperationFormComponent implements AfterViewInit, OnInit {
	@ViewChild('elementNome', { static : false }) nomeElement: ElementRef;
	public displayedColumns: string[] = ['atividadeId', 'nome', 'actions'];
	public formEntity: FormGroup;
	public id: string;
	public entity: Operacao;
	public tipoPerfis: Perfil[] = [];
	public dataSource = new MatTableDataSource<Operacao>();
	public length: number;
	public loader: boolean = false;
	public operacoes: Operacao[] = [];

	constructor(
		private activatedRoute: ActivatedRoute,
		private formBuilder: FormBuilder,
		private service: OperationService,
		private router: Router,
		private matSnackBar: MatSnackBar,
		private serviceOperationroup: OperationGroupService,
		private cdRef: ChangeDetectorRef
	) {}

	ngOnInit() {
		this.loader = true;
		this.id = this.activatedRoute.snapshot.paramMap.get('id');
		this.formEntity = this.formBuilder.group({
			nome: ['', Validators.required],
			operacaoGrupoId : ['', Validators.required],
		});

		this.serviceOperationroup.list().subscribe(
			response => {
				this.operacoes = response.data;
				if (this.id)
					this.get();
				else 
					this.loader = false;
			},
			error => {
				this.loader = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	ngAfterViewInit() {
		this.nomeElement.nativeElement.focus();
	}	
	ngAfterViewChecked()
	{
	  this.cdRef.detectChanges();
	}

	public get(): void {
		this.service.get(this.id).subscribe(
			response => {
				this.entity = response.data;
				this.dataSource = new MatTableDataSource<Operacao>(response.data.atividades);
				this.length = response.data.atividades.length;						
				this.formEntity.controls['nome'].setValue(this.entity.nome);
				this.formEntity.controls['descricao'].setValue(this.entity.descricao);
				this.formEntity.controls['data'].setValue(this.entity.data);
				this.formEntity.controls['local'].setValue(this.entity.local);
				this.formEntity.controls['operacaoGrupoId'].setValue(this.entity.grupo.operacaoGrupoId);
				this.loader = false;
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loader = false;
			}
		);
	}

	public save(): void {
		this.loader = true;
		if (this.formEntity.valid) {
			this.service.save(this.formEntity.value, this.id).subscribe(
				response => {
					SnackbarComponent.showSuccess(this.matSnackBar);

					this.router.navigate(['/configuracao/operacoes']);
					this.loader = false;
				}
			);
		}
	}

}



