
export class MotivosRejeicao {

    public motivoRejeicaoId: number;
    public nome: string;
    public ativo: boolean;
    public operacaoGrupos?: [];

    constructor (
        motivoRejeicaoId?: number,
        nome?: string,
        ativo?: boolean,
        operacaoGrupos?: []
    ) {
        this.motivoRejeicaoId = motivoRejeicaoId;
        this.nome = nome;
        this.ativo = ativo;
        this.operacaoGrupos = operacaoGrupos;
    }
}
