import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatTableDataSource, MatPaginator, MatSnackBar } from '@angular/material';
import { tap } from 'rxjs/operators';
import { PageEvent } from '@angular/material/paginator';
import { OperationService } from '../../services/operation.service';
import { Subject } from 'rxjs';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { Operacao } from 'src/app/models/operacao.interface';
import { Router } from '@angular/router';
import { OperationGroupService } from '../../services/operation-group.service';

@Component({
	selector: 'app-operation-list',
	templateUrl: './operation-list.component.html',
	styleUrls: ['./operation-list.component.scss']
})
export class OperationListComponent implements OnInit, AfterViewInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public formFilter: FormGroup;
	public displayedColumns: string[] = ['id', 'nome', 'descricao','local','grupo', 'actions'];
	public dataSource = new MatTableDataSource<Operacao>();
	public length: number;
	public pageSize = 10;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 100];
	public filterDebounce = new Subject<string>();
	public loader: boolean = false;
	public operacoes: Operacao[] = [];

	constructor(
		private formBuilder: FormBuilder,
		private service: OperationService,
		private serviceOperationroup: OperationGroupService,
		private matSnackBar: MatSnackBar,
		private router: Router,
	) { 
	}
	
	ngOnInit() {
		this.formFilter = this.formBuilder.group({
			nome : [''],
			operacaoGrupoId : [''],
		});

		this.serviceOperationroup.list().subscribe(
			response => {
				this.operacoes = response.data;
				this.find();
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	ngAfterViewInit() {
		if (this.paginator) {
			this.paginator.page.pipe(
				tap(() => this.find())
			).subscribe();
		}
    }

	public find(): void {
		this.loader = true;
		const pageIndex = (this.pageIndex  + 1).toString();
		const pageSize = this.pageSize.toString();
		this.service.find(pageIndex, pageSize, this.formFilter.value.nome, this.formFilter.value.operacaoGrupoId).subscribe(
			response => {
				this.loader = false;
				this.dataSource = new MatTableDataSource<Operacao>(response.data);
				this.length = response.length;
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loader = false;
			}
		);
	}

	public newRegister(): void {
		this.router.navigate(['/configuracao/operacoes/formulario']);
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.find();
	}
}

	

