import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { ResponseList } from 'src/app/models/response-list.interface';
import { OperacaoGrupo } from 'src/app/models/operacao-group.interface';
import { ResponseObject } from 'src/app/models/response-object.interface';

@Injectable({
	providedIn: 'root'
})
export class OperationGroupService {
    private urlController: string = '/api/v1/operacao-grupos';
    constructor (
        private http: HttpClient,
    ) {}

    public list(): Observable<ResponseList> {
        return this.http.get<ResponseList>(`${environment.urlServices}${this.urlController}/listar`, {})
    }

    public find(pageIndex: string, pageSize: string, filter: string ): Observable<ResponseList> {
        return this.http.get<ResponseList>(`${environment.urlServices}${this.urlController}`, {
            params : {
                PageSize: pageSize,
                PageIndex: pageIndex,
                Nome: filter
            }
        })
    }

    public get(id: string): Observable<ResponseObject> {
        return this.http.get<ResponseObject>(`${environment.urlServices}${this.urlController}/${id}`);
    }


    public save(entity: OperacaoGrupo, id: string): Observable<any> {
        if (id){
            return this.put(entity, id);
        }else{
            return this.post(entity);
        }
    }

    public post(entity: OperacaoGrupo): Observable<any> {
        return this.http.post<OperacaoGrupo>(`${environment.urlServices}${this.urlController}`, entity)
    }

    public put(entity: OperacaoGrupo, id: string): Observable<any> {
        return this.http.put<OperacaoGrupo>(`${environment.urlServices}${this.urlController}/${id}`, entity)
    }
}