import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Perfil } from 'src/app/models/perfil.interface';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar, MatTableDataSource, MatDialog } from '@angular/material';
import { ActivityService } from '../../services/activity.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { OperacaoGrupo } from 'src/app/models/operacao-group.interface';
import { AtividadeAcao, CentroCusto } from 'src/app/models/atividade-acao.interface';
import { MotivosRejeicao } from 'src/app/models/motivo-rejeicao.interface';
import { OperationGroupService } from '../../services/operation-group.service';
import { forkJoin } from 'rxjs';
import { MotivosRejeicaoService } from '../../services/motivos-rejeicao.service';
import { FormValidator } from 'src/app/shared/validators/form-validators';
import { AtividadeWorkflow } from 'src/app/models/atividade-workflow.interface';
import { CustomConvertDate } from 'src/app/shared/dateAdapter/custom-convert-date';

@Component({
	selector: 'app-activity-form',
	templateUrl: './activity-form.component.html',
	styleUrls: ['./activity-form.component.scss']
})
export class ActivityFormComponent implements OnInit {
	@ViewChild('elementNome', { static: false }) nomeElement: ElementRef;
	public displayedColumnsWorkflow: string[] = ['tipo', 'execucao', 'tarefa', 'actions'];
	public displayedColumnsAction: string[] = ['id', 'nome', 'valor', 'codigoExterno', 'centroCusto', 'actions'];
	public displayedColumnsReason: string[] = ['nome', 'actions'];
	public formEntity: FormGroup;
	public formAction: FormGroup;
	public formReason: FormGroup;
	public formWorkflow: FormGroup;
	public id: string;
	public entity: Atividade;
	public tipoPerfis: Perfil[] = [];
	public dataSourceAcoes = new MatTableDataSource<AtividadeAcao>();
	public dataSourceMotivos = new MatTableDataSource<MotivosRejeicao>();
	public dataSourceWorkflows = new MatTableDataSource<AtividadeWorkflow>();
	public length: number;
	public loader: boolean = false;
	public operacoes: Operacao[] = [];
	public operacaoGrupos: OperacaoGrupo[] = [];
	public atividades: Atividade[] = [];
	public motivosRejeicao: MotivosRejeicao[] = [];
	public workflows: AtividadeWorkflow[] = [];
	public listaExecucaoWorkflow: any[];
	public listaTipoWorkflow: any[];
	public listaExecucaoWorkflowSelect: any[];
	public centroCustos: any[];

	constructor(
		private activatedRoute: ActivatedRoute,
		private formBuilder: FormBuilder,
		private service: ActivityService,
		private router: Router,
		private matSnackBar: MatSnackBar,
		private serviceOperationGroup: OperationGroupService,
		private serviceReason: MotivosRejeicaoService,
		public dialog: MatDialog,
	) { }

	ngOnInit() {

		this.listaExecucaoWorkflow = [
			{ id: 1, nome: 'Sempre', tipo: 2 },
			{ id: 2, nome: 'Quando Aprovação', tipo: 1 },
			{ id: 3, nome: 'Quando Reprovação', tipo: 1 }
		];

		this.listaTipoWorkflow = [
			{ id: 1, nome: 'Derivação' },
			{ id: 2, nome: 'Paralelismo' },
		];


		this.loader = true;
		this.id = this.activatedRoute.snapshot.paramMap.get('id');
		this.formEntity = this.formBuilder.group({
			operacaoGrupoId: ['', Validators.required],
			operacaoId: ['', Validators.required],
			nome: ['', Validators.required],
			tipoPessoa: ['1', Validators.required],
			controlaAutenticidade: ['false', Validators.required],
			requerDocumentoExtra: ['false', Validators.required],
			moduloAnaliseServiceCenter: ['false', Validators.required],
			sla: ['', [Validators.required, FormValidator.horaMinutoTimeSpan]],
			leadTime: ['', [Validators.required, FormValidator.horaMinutoTimeSpan]],
			validacaoDocumento: [1,Validators.required]
		});

		this.formAction = this.formBuilder.group({
			nome: ['', Validators.required],
			valor: ['', Validators.required],
			codigoExterno: ['', Validators.required],
			centroCusto: ['', Validators.required]
		});

		this.formReason = this.formBuilder.group({
			motivoRejeicao: ['']
		});

		this.formWorkflow = this.formBuilder.group({
			tipo: [''],
			execucao: [''],
			workflowAtividadeId: ['', Validators.required]
		});


		this.get(this.id);
	}

	public linkAction(): void {
		if (this.formAction.valid) {
			const cCusto: CentroCusto = { id: this.formAction.value.centroCusto, nome: '' };

			const action: AtividadeAcao = {
				codigoExterno: this.formAction.value.codigoExterno,
				valor: this.formAction.value.valor,
				nome: this.formAction.value.nome,
				id: 0,
				centroCusto: cCusto
			};

			this.entity.acoes.push(action);
			this.dataSourceAcoes = new MatTableDataSource<AtividadeAcao>(this.entity.acoes);
			this.formAction.reset();
		} else {
			SnackbarComponent.showAlert(this.matSnackBar, "Favor reveja os campos obrigatórios ao associar uma ação");
		}
	}

	public unlinkAction(item): void {
		this.entity.acoes = this.entity.acoes.filter(x => x !== item);
		this.dataSourceAcoes = new MatTableDataSource<AtividadeAcao>(this.entity.acoes);
	}

	public unlinkReason(item): void {
		this.entity.motivosRejeicao = this.entity.motivosRejeicao.filter(x => x !== item);
		this.dataSourceMotivos = new MatTableDataSource<MotivosRejeicao>(this.entity.motivosRejeicao);
	}

	public linkReason(): void {
		if (this.formReason.valid) {
			const exist = this.entity.motivosRejeicao.find(x => x.id === this.formReason.value.motivoRejeicao);
			if (!exist) {
				const descricaoMotivo = this.motivosRejeicao.find(x => x.motivoRejeicaoId === this.formReason.value.motivoRejeicao);
				const motivo: MotivosRejeicao = {
					id: this.formReason.value.motivoRejeicao,
					motivoRejeicaoId: this.formReason.value.motivoRejeicao,
					nome: descricaoMotivo.nome,
					descricao: descricaoMotivo.nome
				};

				this.entity.motivosRejeicao.push(motivo);
				this.dataSourceMotivos = new MatTableDataSource<MotivosRejeicao>(this.entity.motivosRejeicao);
			}
		} else {
			SnackbarComponent.showAlert(this.matSnackBar, "Favor reveja os campos obrigatórios ao associar um motivo de rejeição");
		}
	}

	public unlinkWorkflow(item): void {
		this.entity.workflows = this.entity.workflows.filter(x => x !== item);
		this.dataSourceWorkflows = new MatTableDataSource<AtividadeWorkflow>(this.entity.workflows);
	}

	public linkWorkflow(): void {
		if (this.formWorkflow.valid) {
			const exist = this.entity.workflows.find(x => x.tipoWorkflow.id === this.formWorkflow.value.tipo && x.tipoExecucaoWorkflow.id === this.formWorkflow.value.execucao && x.atividadeTarefa.id === this.formWorkflow.value.workflowAtividadeId);
			if (!exist) {
				const atividadeSelect = this.atividades.find(x => x.id === this.formWorkflow.value.workflowAtividadeId);
				const tipoSelect = this.listaTipoWorkflow.find(x => x.id === this.formWorkflow.value.tipo);
				const execucaoSelect = this.listaExecucaoWorkflow.find(x => x.id === this.formWorkflow.value.execucao);
				const work: AtividadeWorkflow = {
					id: 0,
					tipoWorkflow: {
						id: this.formWorkflow.value.tipo,
						nome: tipoSelect.nome
					},
					tipoExecucaoWorkflow: {
						id: this.formWorkflow.value.execucao,
						nome: execucaoSelect.nome
					},
					atividadeTarefa: {
						id: this.formWorkflow.value.workflowAtividadeId,
						nome: atividadeSelect.nome
					},
				};

				this.entity.workflows.push(work);
				this.dataSourceWorkflows = new MatTableDataSource<AtividadeWorkflow>(this.entity.workflows);
			}
		} else {
			SnackbarComponent.showAlert(this.matSnackBar, "Favor reveja os campos obrigatórios ao associar um workflow");
		}
	}

	public transformObject(): void {
		this.entity.id = +this.id;
		this.entity.operacaoId = this.formEntity.value.operacaoId;
		this.entity.nome = this.formEntity.value.nome;
		this.entity.tipoPessoaId = this.formEntity.value.tipoPessoa;	
		this.entity.controlaAutenticidade = this.formEntity.value.controlaAutenticidade;
		this.entity.requerDocumentoExtra = this.formEntity.value.requerDocumentoExtra;
		this.entity.moduloAnaliseServiceCenter = this.formEntity.value.moduloAnaliseServiceCenter;
		this.entity.validacaoDocumento = Number(this.formEntity.value.validacaoDocumento);
	
		this.entity.sla = CustomConvertDate.diaHoraToTimeSpan(this.formEntity.value.sla);
		this.entity.leadTime = CustomConvertDate.diaHoraToTimeSpan(this.formEntity.value.leadTime);
	}

	public save(): void {
		if (this.formEntity.valid) {
			this.loader = true;

			this.transformObject();

			this.service.save(this.entity).subscribe(
				response => {
					SnackbarComponent.showSuccess(this.matSnackBar);

					this.router.navigate(['/configuracao/atividades']);
					this.loader = false;
				}
				, error => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loader = false;
				});
		} else {
			this.formEntity.markAllAsTouched();
		}
	}

	public setOperation(): void {
		this.formEntity.controls['operacaoId'].setValue(null);
		this.operacoes = this.operacaoGrupos
			.find(x => x.operacaoGrupoId === this.formEntity.value.operacaoGrupoId).operacoes;
	}

	public setTipoWorkflow(): void {
		this.formWorkflow.controls['execucao'].setValue(null);
		this.listaExecucaoWorkflowSelect = this.listaExecucaoWorkflow
			.filter(x => x.tipo === this.formWorkflow.value.tipo);
	}

	public get(id: string): void {
		forkJoin(
			this.service.centroCustos(),
			this.serviceOperationGroup.list(),
			this.serviceReason.list(),
			this.service.getAll(),
			this.service.get(this.id)
		).subscribe(
			([_listCentroCusto, _listOperationGroup, _listReason, _listActiviity, _entity]) => {
				this.centroCustos = _listCentroCusto.data;
				this.operacaoGrupos = _listOperationGroup.data;
				this.motivosRejeicao = _listReason.data;
				this.atividades = _listActiviity.data;

				if (_entity) {
					this.entity = _entity.data;
					this.operacoes = this.operacaoGrupos
						.find(x => x.operacaoGrupoId === this.entity.operacao.grupo.operacaoGrupoId).operacoes;

					this.formEntity.controls['nome'].setValue(this.entity.nome);
					this.formEntity.controls['operacaoGrupoId'].setValue(this.entity.operacao.grupo.operacaoGrupoId);
					this.formEntity.controls['operacaoId'].setValue(this.entity.operacao.operacaoId);

					this.formEntity.controls['leadTime'].setValue(CustomConvertDate.timeSpanToDiaHora(this.entity.leadTime));
					this.formEntity.controls['sla'].setValue(CustomConvertDate.timeSpanToDiaHora(this.entity.sla));
					this.formEntity.controls['tipoPessoa'].setValue(String(this.entity.tipoPessoaId));
					this.formEntity.controls['controlaAutenticidade'].setValue(String(this.entity.controlaAutenticidade));
					this.formEntity.controls['requerDocumentoExtra'].setValue(String(this.entity.requerDocumentoExtra));
					this.formEntity.controls['moduloAnaliseServiceCenter'].setValue(String(this.entity.moduloAnaliseServiceCenter));
					this.formEntity.controls['validacaoDocumento'].setValue(String(this.entity.validacaoDocumento));
				
					this.dataSourceAcoes = new MatTableDataSource<AtividadeAcao>(_entity.data.acoes);
					this.dataSourceMotivos = new MatTableDataSource<MotivosRejeicao>(_entity.data.motivosRejeicao);
					this.dataSourceWorkflows = new MatTableDataSource<AtividadeWorkflow>(_entity.data.workflows);
				} else {
					this.entity = {
						acoes: [],
						motivosRejeicao: [],
						atividadeFilha: false,
						controlaAutenticidade: false,
						requerDocumentoExtra: false,
						moduloAnaliseServiceCenter: false,
						operacao: null,
						template: null,
						fila: null,
						operacaoId: null,
						tipoPessoa: null,
						pessoaJuridica: null,
						tipoPessoaId: null,
						leadTime: null,
						sla: null,
						nome: null,
						codigoExterno: null,
						hasTemplate: null,
						id: null,
						workflows: [],
						validacaoDocumento: 0
					};
				}
				this.loader = false;
			}
			, error => {
				this.loader = false;
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}
}



