import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { ResponseList } from 'src/app/models/response-list.interface';
import { MotivosRejeicao } from 'src/app/models/motivos-rejeicao.model';

@Injectable({
	providedIn: 'root'
})
export class MotivosRejeicaoService {

    public motivosRejeicaoUrl="/api/v1/motivos-rejeicao"

    constructor (
        private http: HttpClient,
    ) {}
    public list(): Observable<any> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosRejeicaoUrl + '/listar')
    }

    public getAll(descricaoMotivo: string, pageSize: string, pageIndex: string): Observable<any> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosRejeicaoUrl, {
            params : {
                DescricaoMotivo: descricaoMotivo,
                PageSize: pageSize,
                PageIndex: pageIndex
            }
        })
    }

    public getAtividades(): Observable<any> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosRejeicaoUrl + '/novo');
    }

    public get(motivoRejeicaoId: string): Observable<any> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosRejeicaoUrl + '/' + motivoRejeicaoId)
    }

    public post(nome: string, ativo: boolean,  atividades: any): Observable<any> {
        return this.http.post<MotivosRejeicao>(environment.urlServices + this.motivosRejeicaoUrl, {
            "nome": nome,
            "ativo": ativo,
            "atividades": atividades
        })
    }

    public put(motivoRejeicaoId: string, nome: string, ativo: boolean,  atividades: any): Observable<any> {
        return this.http.put<MotivosRejeicao>(environment.urlServices + this.motivosRejeicaoUrl + '/' + motivoRejeicaoId, {
            "nome": nome,
            "ativo": ativo,
            "atividades": atividades
        })
    }

    public delete(id: number): Observable<ResponseList> {
        return this.http.get<ResponseList>(environment.urlServices + this.motivosRejeicaoUrl + '/delete/' + id)
    }
}