import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { ResponseList } from 'src/app/models/response-list.interface';
import { Operacao } from 'src/app/models/operacao.interface';
import { ResponseObject } from 'src/app/models/response-object.interface';

@Injectable({
	providedIn: 'root'
})
export class OperationService {
    private urlController: string = '/api/v1/operacoes';
    constructor (
        private http: HttpClient,
    ) {}

    public list(): Observable<ResponseList> {
        return this.http.get<ResponseList>(`${environment.urlServices}${this.urlController}`, {})
    }


    public find(pageIndex: string, pageSize: string, nome: string, operacaoGrupoId: string ): Observable<ResponseList> {
        return this.http.get<ResponseList>(`${environment.urlServices}${this.urlController}/listar`, {
            params : {
                PageSize: pageSize,
                PageIndex: pageIndex,
                Nome: nome,
                OperacaoGrupoId: operacaoGrupoId || ""
            }
        })
    }

    public get(id: string): Observable<ResponseObject> {
        return this.http.get<ResponseObject>(`${environment.urlServices}${this.urlController}/${id}`);
    }


    public save(entity: Operacao, id: string): Observable<any> {
        if (id){
            return this.put(entity, id);
        }else{
            return this.post(entity);
        }
    }

    public post(entity: Operacao): Observable<any> {
        return this.http.post<Operacao>(`${environment.urlServices}${this.urlController}`, entity)
    }

    public put(entity: Operacao, id: string): Observable<any> {
        return this.http.put<Operacao>(`${environment.urlServices}${this.urlController}/${id}`, entity)
    }
}