import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListTemplateComponent } from './list-template/list-template.component';
import { CreateTemplateComponent } from './create-template/create-template.component';
import { PreviewTemplateComponent } from './preview-template/preview-template.component';
import { StepComponent } from './step/step.component';
import { FieldGroupComponent } from './field-group/field-group.component';
import { OperationGroupListComponent } from './operation-group/operation-group-list/operation-group-list.component';
import { OperationGroupFormComponent } from './operation-group/operation-group-form/operation-group-form.component';
import { OperationFormComponent } from './operation/operation-form/operation-form.component';
import { OperationListComponent } from './operation/operation-list/operation-list.component';
import { ActivityListComponent } from './activity/activity-list/activity-list.component';
import { ActivityFormComponent } from './activity/activity-form/activity-form.component';
import { ConfigurationSystemRoutesGuard } from './configuration-system-routes.guard';
import { MotivosRejeicaoComponent } from './motivos-rejeicao/motivos-rejeicao.component';
import { AddMotivosRejeicaoComponent } from './add-motivos-rejeicao/add-motivos-rejeicao.component';
import { ButtonsDecisionComponent } from './buttons-decision/buttons-decision.component';
import { MotivosContingenciaComponent } from './motivos-contingencia/motivos-contingencia.component';
import { AddMotivosContingenciaComponent } from './add-motivos-contingencia/add-motivos-contingencia.component';

const routes: Routes = [
	{ path: 'lista-templates', component: ListTemplateComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'template/:idtemplate', component: CreateTemplateComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'template/:idtemplate/preview', component: PreviewTemplateComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'template/:idtemplate/botoes-decisao', component: ButtonsDecisionComponent, canActivate: [ ConfigurationSystemRoutesGuard ]},
	{ path: 'template/:idtemplate/:idClone', component: CreateTemplateComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'template/:idtemplate/step/:stepIndex', component: StepComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'template/:idtemplate/step/:stepIndex/grupo-campos/:fieldGroupIndex', component: FieldGroupComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'operacao-grupos', component: OperationGroupListComponent },
	{ path: 'operacao-grupos/formulario', component: OperationGroupFormComponent },
	{ path: 'operacao-grupos/formulario/:id', component: OperationGroupFormComponent },
	{ path: 'operacoes', component: OperationListComponent },
	{ path: 'operacoes/formulario', component: OperationFormComponent },
	{ path: 'operacoes/formulario/:id', component: OperationFormComponent },	
	{ path: 'atividades', component: ActivityListComponent },
	{ path: 'atividades/formulario', component: ActivityFormComponent },
	{ path: 'atividades/formulario/:id', component: ActivityFormComponent },	
	{ path: 'motivos-rejeicao', component: MotivosRejeicaoComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'add-motivos-rejeicao', component: AddMotivosRejeicaoComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'add-motivos-rejeicao/:motivoRejeicaoId', component: AddMotivosRejeicaoComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'motivos-contingencia', component: MotivosContingenciaComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'add-motivos-contingencia', component: AddMotivosContingenciaComponent, canActivate: [ ConfigurationSystemRoutesGuard ] },
	{ path: 'add-motivos-contingencia/:motivoContingenciaId', component: AddMotivosContingenciaComponent, canActivate: [ ConfigurationSystemRoutesGuard ] }	
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class ConfigurationSystemRoutingModule { }
