
export class MotivosContingencia {

    public motivoContingenciaId: number;
    public nome: string;
    public ativo: boolean;
    public operacaoGrupos?: [];

    constructor (
        motivoContingenciaId?: number,
        nome?: string,
        ativo?: boolean,
        operacaoGrupos?: []
    ) {
        this.motivoContingenciaId = motivoContingenciaId;
        this.nome = nome;
        this.ativo = ativo;
        this.operacaoGrupos = operacaoGrupos;
    }
}
