import { Component, OnInit, Inject} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Tree } from 'src/app/models/tree.model';
import { FlatTreeControl } from '@angular/cdk/tree';
import { TreeFlatNode } from 'src/app/models/tree-flat-node.model';

@Component({
	selector: 'from-to-dialog',
	templateUrl: './dialog-motivos-rejeicao.component.html',
	styleUrls: ['./dialog-motivos-rejeicao.component.scss']
})

export class DialogMotivosRejeicaoComponent implements OnInit {
	public loader: boolean = false;
	public treeFila: Tree[] = [];
	public treeControl: FlatTreeControl<TreeFlatNode>;

	constructor(
		public dialogRef: MatDialogRef<DialogMotivosRejeicaoComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any
	) { }
	
	ngOnInit() {
		this.treeFila = this.data;
	}

	public cancel(): void {
		this.dialogRef.close();
	}

	atualizar(): void {
		this.dialogRef.close(this.treeControl);
	}

	public getData($event): void{
		this.treeControl = $event;
	}
}
