import { Component, OnInit, ViewChild } from '@angular/core';
import { PostTemplate } from 'src/app/models/post-template.class';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Field, FieldGroup, Step } from 'src/app/models/template.model';
import { ActivatedRoute } from '@angular/router';
import { Dialog } from 'src/app/models/dialog.model';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { ConfigurationServiceUtilService } from '../services/configuration-system-util.service';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { AddFieldFormComponent } from '../../../shared/components/add-field-form/add-field-form.component';


@Component({
	selector: 'app-field-group',
	templateUrl: './field-group.component.html',
	styleUrls: ['./field-group.component.scss']
})
export class FieldGroupComponent implements OnInit {

	@ViewChild(AddFieldFormComponent, {static : true}) addFieldFormComponent: AddFieldFormComponent;

	public postTemplate: PostTemplate;
	public templateId: string;
	public displayedColumns: string[] = ['item', 'nameField', 'type', 'visible', 'enable', 'required', 'width', 'edit', 'remove', 'moveToUp', 'moveToDown'];
	public dialogRef: MatDialogRef<DialogComponent, any>;

	/** Todos os campos do template */
	public formFields: Field[] = [];

	/** Referências do Passo e do Grupo de Campos */
	public step: Step;
	public fieldGroup: FieldGroup;
	public stepIndex: number;
	private fieldGroupIndex: number;
	public editingField: boolean;

	constructor (
		private activatedRoute: ActivatedRoute,
		public dialog: MatDialog,
		public utilService: ConfigurationServiceUtilService,
	) { }
	
	ngOnInit() {
		this.templateId = this.activatedRoute.snapshot.paramMap.get('idtemplate');
		this.stepIndex = Number(this.activatedRoute.snapshot.paramMap.get('stepIndex'));
		this.fieldGroupIndex = Number(this.activatedRoute.snapshot.paramMap.get('fieldGroupIndex'));
		this.postTemplate = this.getData();

		if (this.postTemplate) {
			this.postTemplate.template.steps.forEach(step => {
				if (step.index === this.stepIndex) {
					this.step = step;
					step.fieldGroups.forEach(fieldGroup => {
						if (fieldGroup.index === this.fieldGroupIndex) {
							this.fieldGroup = fieldGroup;
						}
					});
				}
			})

			this.postTemplate.template.steps.map(step => {
				step.fieldGroups.map(fieldGroup => {
					fieldGroup.fields.map(field => {
						this.formFields = [...this.formFields, field];
					})
				})
			})
		}
	}

	public addField(field: Field): void {
		if (this.editingField) {
			const indexformFields: number = this.formFields.map(el => el.name).indexOf(field.name);
			this.formFields[indexformFields] = field;
			const indexfieldGroup: number = this.fieldGroup.fields.map(el => el.name).indexOf(field.name);
			this.fieldGroup.fields[indexfieldGroup] = field;
			this.editingField = false;
		} else {
			this.formFields = [...this.formFields, field];
			this.fieldGroup.fields = [...this.fieldGroup.fields, field];
		}
		this.save(true);
	}

	public editField(field: Field): void {
		this.editingField = true;
		this.addFieldFormComponent.editField(field);
	}

	public removeField(field: Field): void {
		let dialog: Dialog = new Dialog();
		dialog.title = "Exclusão de Campo";
		dialog.message = `Você tem certeza que deseja excluir o campo '${field.name}'?`;
		dialog.type = DialogTypes.CONFIRMATION;
		this.showMessage(dialog);

		this.dialogRef.afterClosed().subscribe(
			response => {
				if (response === 'ok') {
					this.formFields = this.formFields.filter(element => element.name !== field.name);
					this.fieldGroup.fields = this.fieldGroup.fields.filter(element => element.name !== field.name);

					this.formFields.forEach(element => {
						element.conditionsToVisible = element.conditionsToVisible.filter(condition => condition.fieldName !== field.name);
						element.conditionsToEnable = element.conditionsToEnable.filter(condition => condition.fieldName !== field.name);
					});
					this.save();
				}
			}
		);
	}

	public drop(event: CdkDragDrop<string[]>) {
		moveItemInArray(this.fieldGroup.fields, event.previousIndex, event.currentIndex);
		this.save();
	}

	public getData(): PostTemplate {
		return JSON.parse(sessionStorage.getItem('postTemplate'));
	}

	public save(showMessage?: boolean): void {
		
		sessionStorage.setItem('postTemplate', JSON.stringify(this.postTemplate));

		if (showMessage) {
			let dialog: Dialog = new Dialog();
			dialog.title = "Sucesso";
			dialog.message = "Operação realizada com sucesso!";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
		}
	}

	public getIndex(index: string): string {
		return (Number(index) + 1).toString();
	}

	public showMessage(dialog: Dialog): void {
		this.dialogRef = this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title : dialog.title,
				message : dialog.message,
				type : dialog.type,
			}
		});
	}
}